import axios from "axios";

/// класс обеспечивающий получение данных на frontend
class ApiProvider {
    static apiUrl = 'http://andreexz.beget.tech/ajax.php';

    static getMapPoints = async () => {
        let points = [];
        const response = await axios.post(this.apiUrl, {
            method: 'map_points'
        })
        if (response.status === 200 && response.data['points'].length > 0) {
            points = response.data['points'];
        }
        return points;
    }
}

export default ApiProvider;